html, body, #root, #app {
  margin: 0;
  padding: 0;
}

html {
  width: 100%;
}

body {
  height: 100vh;
  width: 100%;
  overflow-x: auto;
  overflow-y: auto;
}

#root, #app {
  height: 100%;
  width: 100%;
}

#main-page, #about-page, #portfolio-page, #contact-page {
  height: 100%;
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  align-items: stretch;
  font-family: "Source Sans Pro", sans-serif;
}

#name-card {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  background-image: url("./static assets/static.gif");
  background-size: cover;
  background-repeat: no-repeat;
  width: 60%;
  height: 100%;
}

#name-title {
  color: white;
  font-size: 7.5rem;
  animation-name: name-title;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  font-family: "Source Sans Pro", sans-serif;
}

@keyframes name-title {
  0% {
    font-family: "Source Sans Pro", sans-serif;
  }
  20% {
    font-family: "Finger Paint", sans-serif;
  }
  40% {
    font-family: "Coming Soon", sans-serif;
  }
  60% {
    font-family: "Knewave", sans-serif;
  }
  80% {
    font-family: "Architects Daughter", sans-serif;
  }
}

#button-card {
  width: 40%;
}

#button-card, #about-title-card, #portfolio-title-card, #contact-title-card {
  height: 100%;
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
}

#button-card {
  flex-flow: column nowrap;
}

.links {
  margin: 15px 0;
  border: none;
  text-decoration: none;
  background: black;
  font-family: inherit;
  font-size: 1.75rem;
  color: white;
  width: 200px;
  text-align: center;
  font-family: 'Source Sans Pro', sans-serif;
}

.links:hover {
  color: white;
  text-decoration: none;
}

.back {
  background: none;
  border: none;
  color: white;
  font-family: "Knewave", sans-serif;
  font-size: 1.5rem;
  width: 1.5rem;
  height: 2rem;
  text-align: right;
  white-space: nowrap;
  transition: all 1s;
  overflow: hidden;
  direction: rtl;
}

.back:hover {
  width: 10rem;
}

.back:focus {
  outline: none;
}

.back > i {
  pointer-events: none;
}

#about-title-card {
  
  overflow-wrap: break-word;
  padding: 20px;
  position: fixed;
  top: 0;
  left: 0;
  width: 45%;
}

#portfolio-title-card {
  
  overflow-wrap: break-word;
  padding: 20px;
  width: 45%;
  position: fixed;
  right: 0;
  top: 0;
}

#contact-title-card {
  
  overflow-wrap: break-word;
  padding: 20px;
  position: fixed;
  width: 55%;
  top: 0;
  right: 0;
}

#about-title, #portfolio-title, #contact-title {
  color: white;
  font-family: "Knewave", sans-serif;
  font-size: 7.5rem;
  max-width: 100%;
  text-align: center;
}

#about-page {
  background-image: url("./static assets/mountains.gif");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 25% 0%;
  width: 100%;
  height: 100%;
}

#about-main-card {
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: flex-start;
  overflow-y: auto;
  width: 55%;
  min-height: 100%;
  height: 100vh;
  margin-left: 45%;
  padding: 20px;
}

#about-back {
  position: absolute;
  top: 20px;
  left: 20px;
}

#about-text {
  border-radius: .25rem;
  width: 95%;
  height: 95%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: stretch;
}

.about-header {
 border: 1px solid #d3d3d3;
 color: #4c67c4;
 font-size: 2rem;
 display: flex;
 flex-flow: row nowrap;
 justify-content: space-between;
 align-items: center;
 font-size: 2rem;
 cursor: pointer;
}

.about-header h2 {
  pointer-events: none;
}

.about-header > button {
  background: none;
  border: none;
  font-size: 3rem;
  pointer-events: none;
  color: inherit;
}

.about-header > button:focus {
  outline: none;
}

.about-paragraph {
  border-left: 1px solid #d3d3d3;
  border-right: 1px solid #d3d3d3;
}

.about-paragraph > p, ul {
  color: black;
}


#main-1 {
  direction: ltr;
  text-align: left;
}

#portfolio-page {
  background-image: url("./static assets/ocean.gif");
  background-size: cover;
  background-repeat: no-repeat;
  overflow-x: hidden;
}

#portfolio-main-card {
  width: 55%;
  overflow-y: auto;
  margin-right: 45%;
  min-height: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;
  padding-top: 5vh;
}

#portfolio-back {
  position: absolute;
  top: 20px;
  right: 20px;
}

#project-grid {
  margin-top: 20px;
  margin-bottom: 2rem;
  width: 100%;
  height: 80%;
  display: grid;
  grid-template-columns: 25% 25% 25%;
  grid-template-rows: 1fr 1fr;
  grid-gap: 5%;
  grid-template-areas: "one two three" "four five six";
  place-content: center center;
}

#project-grid a {
  text-decoration: none;
  color: black;
}

#project-grid a:hover {
  color: black;
  text-decoration: none;
}

.card {
  min-width: 120px;
  border: none;
  border-radius: 0.25rem;
}

.card-body {
  padding: 0.75rem;
  color: #ae5647;
  background-color: white;
  border-bottom-left-radius: 0.25rem;
  border-bottom-right-radius: 0.25rem;
  display: grid;
  grid-template-rows: auto 3fr 2fr;
  row-gap: .5rem;
  grid-template-areas:
  "one"
  "two"
  "three";

}

.card-title {
  font-weight: bold;
  color: inherit;
  font-size: 1rem;
  grid-area: one;
  padding: 0;
  margin: 0;
}

.card-subtitle {
  font-size: 0.75rem;
  padding: 0;
  margin: 0;
  color: #73605f;
  grid-area: two;
}

.card-text {
  font-size: 0.75rem;
  align-self: end;
  justify-self: end;
  grid-area: three;
}

#key {
  background-color: white;
  color: #ae5647;
  border-radius: 0.25em;
  width: 7.5%;
  max-height: 10%;
  height: 10%;
  margin-bottom: 5%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  font-size: 0.75rem;
  transition: all 1s;
  overflow: hidden;
}

#key h2 {
  padding: 0;
  padding-left: 0.5rem;
  margin: 0;
}

.key-item {
  padding: 0;
  margin: 0;
  display: none;
  overflow: hidden;
  white-space: nowrap;
  font-size: 0.75rem;
}

@media screen and (max-width: 900px) {
  .key-item {
    font-size: 0.6rem;
  }
}
@media screen and (min-width: 1366px) {
  .key-item br {
    display: none;
  }
}
.key-icon {
  padding-left: 1rem;
  padding-right: 0.5rem;
}

#key:hover {
  width: 85%;
}

#key:hover > .key-item {
  display: block;
}

#last-key {
  padding-right: 0.5rem;
}

#contact-page {
  background-image: url("./static assets/desert.gif");
  background-size: cover;
  background-repeat: no-repeat;
}


#contact-main-card {
  width: 45%;
  min-height: 100%;
  margin-right: 55%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

#contact-back {
  position: absolute;
  top: 20px;
  right: 20px;
}

#contact-links {
  width: 90%;
  height: 90%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-evenly;
  align-items: center;
}

#contact-links li {
  width: 90%;
  padding: 5px 5px 5px 10px;
  border-radius: 0.25rem;
  background-color: white;
  color: #876c58;
}

.contact-link {
  background-color: #bac030;
  border-radius: 0.25rem;
  color: white;
  width: 3rem;
  height: 3rem;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
}
.contact-link i {
  font-size: 1.5rem;
  max-width: 80%;
}

.shadow {
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.5) !important;
}

.link-header {
  font-size: 1.5rem;
}

.contact-link:hover {
  color: white;
  text-decoration: none;
}

@media screen and (max-width: 800px) {
  #main-page {
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: stretch;
    height: calc(60vh + 350px);
  }

  #name-card {
    width: 100%;
    height: 60vh;
  }

  #name-title {
    font-size: 4rem;
    max-width: 80%;
    padding: 20px;
  }

  #button-card {
    width: 100%;
    height: 350px;
  }

  #about-page {
    background: white;
    min-height: 100%;
    width: 100%;
    overflow-x: hidden;
    overflow-y: auto;
  }

  #about-title-card {
    background-image: url('./static\ assets/mountains.gif');
    background-repeat: no-repeat;
    background-size: cover;
    height: 40vh;
    width: 100%;
    position: static;
    background-position: 0% 0%;
  }

  #about-title {
    font-size: 4rem;
    max-width: 80%;
  }

  #about-main-card {
    height: auto;
    min-height: 60%;
    width: 100vw;
    margin: 0;
  }

  #about-text {
    width: 90vw;
    height: auto;
    justify-content: flex-start;
    align-items: center;
  }

  .about-header {
    background-color: #4c67c4;
    color: white;
    margin: 0;
  }

  .about-paragraph {
    background-color: #4c67c4;
    color: white;
  }

  .about-paragraph > p, ul {
    color: white;
  }


  #about-back {
    align-self: flex-start;
  }

  #portfolio-page {
    background: white;
    overflow-y: auto;
    display: flex;
    flex-flow: column-reverse nowrap;
    justify-content: flex-start;
    align-items: stretch;
    height: auto;
  }

  #portfolio-title-card {
    background-image: url('./static\ assets/ocean.gif');
    background-size: cover;
    background-repeat: no-repeat;
    position: static;
    width: 100%;
    min-height: 40%;
    height: 40vh;
  }

  #portfolio-title {
    font-size: 4rem;
  }

  #portfolio-back {
    right: auto;
    left: 20px;
  }

  #main-2 {
    direction: ltr;
    text-align: left;
  }
  #main-2 i {
    transform: scaleX(-1);
  }

  #portfolio-main-card {
    width: 100%;
    margin: 0;
    min-height: 100%;
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-evenly;
    align-items: center;
  }

  #project-grid {
    width: 100%;
    height: auto;
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: center;
  }

  .card {
    min-width: 0;
    width: 75%;
    margin-bottom: 20px;
  }

  .card-body {
    background-color: #644b44;
    color: white;
  }

  .card-subtitle {
    color: #dcc1b5;
  }

  .card-text {
    display: none;
  }

  #key {
    display: none;
  }

  #contact-page {
    background: white;
    min-height: 100%;
    width: 100%;
    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: stretch;
    overflow-y: auto;
  }

  #contact-title-card {
    background-image: url('./static\ assets/desert.gif');
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    min-height: 40%;
    height: 40vh;
    position: relative;
    margin-bottom: 20px;
    order: 1;
  }

  #contact-title {
    font-size: 4rem;
  }

  #contact-back {
    position: absolute;
    top: 20px;
    right: auto;
    left: 20px;
  }
  #contact-back i {
    transform: scaleX(-1);
  }

  #contact-links {
    width: 100%;
    min-height: 100%;
    align-items: center;
  }

  #contact-links li {
    background-color: #876c58;
    color: white;
  }

  .contact-link {
    background-color: #bac030;
    color: white;
  }

  #main-3 {
    direction: ltr;
    text-align: left;
  }

  #contact-main-card {
    order: 2;
    width: 100%;
    min-height: 100%;
    margin: 0;
  }
}

