@mixin flex($flow, $justify, $align) {
    display: flex;
    flex-flow: $flow;
    justify-content: $justify;
    align-items: $align;
}

@mixin background($img, $size, $repeat ) {
    background-image: $img;
    background-size: $size;
    background-repeat: $repeat;
}

@mixin button($border, $dec, $url, $cover, $font) {
    border: $border;
    text-decoration: $dec;
    background-image: $url;
    background-size: $cover;
    font-family: $font;
}



$name3: 'Coming Soon', sans-serif;
$titles: 'Knewave', sans-serif;
$name2: 'Finger Paint', sans-serif;
$name1: 'Architects Daughter', sans-serif;
$texts: 'Source Sans Pro', sans-serif;



html, #root, #app {
    margin: 0;
    padding: 0;
}

body {
    height: 100vh;
    width: 100vw;
    @extend html;
    overflow-x: auto;
    overflow-y: auto;
}

#root, #app {
    height: 100%;
    width: 100%;
}

#main-page, #about-page, #portfolio-page, #contact-page {
    height: 100%;
    width: 100%;
    @include flex(row wrap, flex-start, stretch);
    font-family: $texts;
}

#name-card {
    @include flex(column nowrap, center, center);
    @include background(url('./static\ assets/static.gif'), cover, no-repeat);
    width: 60%;
    height: 100%;
}

#name-title {
    color: white;
    font-size: 7.5rem;
    animation-name: name-title;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    font-family: $name1;
}

@keyframes name-title {
    0% {
        font-family: $name1;
    }

    33% {
        font-family: $name3;
        
    }

    67% {
        font-family: $name2;
    }
}

#button-card {
    width: 40%;
}

#button-card, #about-title-card, #portfolio-title-card, #contact-title-card {
    height: 100%;
    @include flex(column wrap, center, center);
}

.links {
    margin: 15px 0;
    @include button(solid 2px black, none, url('./static\ assets/static.gif'), contain, inherit);
    font-size: 2rem;
    font-weight: bold;
    color: white;
    width: 200px;
    text-align: center;
    font-family: sans-serif;
}

.links:hover {
    color: white;
    text-decoration: none;
}


.back {
    background: none;
    border: none;
    color: white;
    font-family: $titles;
    font-size: 1.5rem;
    width: 1.5rem;
    height: 2rem;
    text-align: right;
    white-space: nowrap;
    transition: all 1s;
    overflow: hidden;
    direction: rtl;
}

.back:hover {
    width: 10rem;
}

.back:focus {
    outline: none;
}

.back > i {
    pointer-events: none;
}


#about-title-card {
    @include background(url('./static\ assets/mountains.gif'), cover, no-repeat);
    background-position: 25% 0%;
    overflow-wrap: break-word;
    padding: 20px;
    position: fixed;
    top: 0;
    left: 0;
    width: 45%;
}

#portfolio-title-card {
    @include background(url('./static\ assets/ocean.gif'), cover, no-repeat);
    overflow-wrap: break-word;
    padding: 20px;
    width: 45%;
    position: fixed;
    right: 0;
    top: 0;

}

#contact-title-card {
    @include background(url('./static\ assets/desert.gif'), cover, no-repeat);
    overflow-wrap: break-word;
    padding: 20px;
    position: fixed;
    width: 55%;
    top: 0;
    right: 0;
}

#about-title, #portfolio-title, #contact-title {
    color: white;
    font-family: $titles;
    font-size: 7.5rem;
    max-width: 100%;
    text-align: center;
}

#about-page {
    width: 100%;
    height: 100%;
}

#about-main-card {
    @include flex(column nowrap, flex-start, flex-start);
    overflow-y: auto;
    width: 55%;
    min-height: 100%;
    height: 100vh;
    margin-left: 45%;
    padding: 20px;
}

#about-back {
    position: absolute;
    top: 20px;
    left: 20px;
}

#main-1 {
    direction: ltr;
    text-align: left;
}

#portfolio-page {
    overflow-x: hidden;
}

#portfolio-main-card {
    background-color: white;
    width: 55%;
    overflow-y: auto;
    margin-right: 45%;
    min-height: 100%;
    @include flex(column nowrap, flex-start, center);
    padding-top: 5vh;
}

#portfolio-back {
    position: absolute;
    top: 20px;
    right: 20px;
}

#project-grid {
    margin-top: 20px;
    margin-bottom: 20px;
    width: 100%;
    height: 80%;
    display: grid;
    grid-template-columns: 25% 25% 25%;
    grid-template-rows: 1fr 1fr;
    grid-gap: 5%;
    grid-template-areas:
    "one two three"
    "four five six";
    place-content: center center;

    a {
        text-decoration: none;
        color: black
    }

    a:hover {
        color: black;
        text-decoration: none;
    }
}

.card {
    min-width: 120px;
    border: none;
    border-radius: .25rem;
}


.card-body {
    padding: .75rem;
   color: white;
    background-color: #644b44;
    border-bottom-left-radius: .25rem;
    border-bottom-right-radius: .25rem;
}

.card-title {
    font-size: 1rem;
}

.card-subtitle {
    font-size: .75rem;
    color: #dcc1b5;
}

.card-text {
    font-size: .75rem;
}

#key {
    background-color: #644b44;
    color: white;
    border-radius: .25em;
    width: 7.5%;
    max-height: 10%;
    height: 10%;
    margin-bottom: 5%;
    @include flex(row nowrap, center, center);
    font-size: .75rem;
    transition: all 1s;
    overflow: hidden;

    h2 {
        padding: 0;
        padding-left: .5rem;
        margin: 0;
    }

    
}

.key-item {
    padding: 0;
    margin: 0;
    display: none;
    overflow: hidden;
    white-space: nowrap;
    font-size: 0.75rem;
}

@media screen and (max-width: 900px) {
    .key-item {
        font-size: 0.6rem;
    }
}

@media screen and (min-width: 1366px) {
    .key-item {
        br {
            display: none;
        }
    }
}

.key-icon {
    padding-left: 1rem;
    padding-right: .5rem;
}

#key:hover {
    width: 85%;
}

#key:hover > .key-item {
    display: block;
}

#last-key {
    padding-right: .5rem;
}

#contact-main-card {
    width: 45%;
    min-height: 100%;
    margin-right: 55%;
    @include flex(column nowrap, center, center);
}

#contact-back {
    position: absolute;
    top: 20px;
    right: 20px;
}

#contact-links {
    width: 90%;
    height: 90%;
    @include flex(column nowrap, space-evenly, flex-start);

    li {
        width: 90%;
        padding: 5px 5px 5px 10px;
        border-radius: .25rem;
        background-color: #876c58;
        color: white;
    }
}

.contact-link {
    background-color:  #bac030;
    border-radius: .25rem;
    color: white;
    width: 3rem;
    height: 3rem;
    @include flex(column nowrap, center, center);
    flex-shrink: 0;
    i {
        font-size: 1.5rem;
        max-width: 80%;
    }
}

.shadow {
    box-shadow: 0 .5rem 1rem rgba(0, 0, 0, 0.5) !important;
}

.link-header {
    font-size: 1.5rem;
}

.contact-link:hover {
    color: white;
    text-decoration: none;
}

@media screen and (max-width: 800px) {
    #main-page {
        @include flex(column nowrap, flex-start, stretch);
        height: 100vh;
    }

    #name-card {
        width: 100%;
        min-height: 60vh;
    }

    #name-title {
        font-size: 4rem;
        max-width: 80%;
        padding: 20px;

    }

    #button-card {
        width: 100%;
        min-height: 40vh;
    }

    #about-page {
        min-height: 100%;
        width: 100%;
    }

    #about-title-card {
        height: 40vh;
        width: 100%;
        position: static;
        background-position: 0% 0%;
    }

    #about-title {
        font-size: 4rem;
        max-width: 80%;
    }

    #about-main-card {
        min-height: 60%;
        width: 100%;
        margin: 0;
    }

    #about-back {
        align-self: flex-start;
    }

    #portfolio-page {
        overflow-y: auto;
        @include flex(column-reverse nowrap, flex-start, stretch);
        height: auto;
    }

    #portfolio-title-card {
        position: static;
        width: 100%;
        min-height: 40%;
        height: 40vh;
    }

    #portfolio-title {
        font-size: 4rem;
    }

    #portfolio-back {
        right: auto;
        left: 20px;
    }

    #main-2 {
        direction: ltr;
        text-align: left;
         
        i {
            transform: scaleX(-1);
        }
    }
    
    #portfolio-main-card {
        width: 100%;
        margin: 0;
        min-height: 100%;
        @include flex(column nowrap, space-evenly, center);
    }
    
    #project-grid {
        width: 100%;
        height: auto;
        @include flex(column nowrap, flex-start, center);
    }
    
    .card {
        min-width: 0;
        width: 75%;
        margin-bottom: 20px;
    }

    .card-text {
        display: none;
    }
    

    #key {
        display: none;
    }

    #contact-page {
        min-height: 100%;
        width: 100%;
        @include flex(column nowrap, flex-start, stretch);
        overflow-y: auto;
    }

    #contact-title-card {
        width: 100%;
        min-height: 40%;
        height: 40vh;
        position: relative;
        margin-bottom: 20px;
        order: 1;
    }

    #contact-title {
        font-size: 4rem;
    }

    #contact-back {
        position: absolute;
        top: 20px;
        right: auto;
        left: 20px;
        i {
            transform: scaleX(-1);
        }
    }

    #contact-links {
        width: 100%;
        min-height: 100%;
        align-items: center;
    }

    #main-3 {
        direction: ltr;
        text-align: left;
    }

    #contact-main-card {
        order: 2;
        width: 100%;
        min-height: 100%;
        margin: 0;
    }

    
}



